.Holder{
    position: fixed;
    width: 100vw;
    height: 100vh;
    background-color: white;
    overflow-y:hidden;
    display: flex;
    //make children center
    justify-content: center;
    align-items: center;
    .Backdrop{
        width: 100%;
        height: 100%;
        object-fit: cover;
        z-index: -1;
    }

}